/* Interactive Elements - ORIGINAL */

/* Bot Companion */
.bot-companion {
  position: fixed;
  width: 35px;
  height: 35px;
  pointer-events: none;
  z-index: 9999;
  transition: left 0.15s ease-out, top 0.15s ease-out;
}

.bot-body {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #00d9ff, #0099ff);
  border-radius: 10px;
  position: relative;
  box-shadow: 0 0 20px rgba(0, 217, 255, 0.5);
  animation: bot-float 3s ease-in-out infinite;
}

.bot-eye {
  position: absolute;
  width: 7px;
  height: 7px;
  background: #0a0a0f;
  border-radius: 50%;
  top: 40%;
  transition: all 0.2s;
}

.bot-eye.left { left: 25%; }
.bot-eye.right { right: 25%; }

.bot-eye.blink {
  height: 2px;
  top: 45%;
}

.zzz {
  position: absolute;
  top: -20px;
  right: -10px;
  color: #00d9ff;
  font-size: 14px;
  font-weight: bold;
  animation: float-up 2s infinite;
}

@keyframes bot-float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

@keyframes float-up {
  0% { opacity: 0; transform: translateY(0); }
  50% { opacity: 1; }
  100% { opacity: 0; transform: translateY(-15px); }
}

/* Language Toggle */
.language-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(15, 15, 25, 0.8);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 1000;
  font-size: 13px;
}

.language-toggle:hover {
  background: rgba(0, 217, 255, 0.1);
  border-color: #00d9ff;
  transform: translateY(-2px);
}

/* Budget Button */
.budget-button {
  position: fixed;
  top: 20px;
  left: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: linear-gradient(135deg, #00d9ff, #0099ff);
  border: none;
  border-radius: 12px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 1000;
  font-size: 13px;
  animation: pulse-glow 3s ease-in-out infinite;
}

.budget-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 217, 255, 0.3);
}

@keyframes pulse-glow {
  0%, 100% { box-shadow: 0 4px 15px rgba(0, 217, 255, 0.4); }
  50% { box-shadow: 0 4px 25px rgba(0, 217, 255, 0.6); }
}

/* Budget Popup */
.budget-popup-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 20px;
  animation: fadeIn 0.3s ease;
}

.budget-popup {
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideUp 0.3s ease;
}

.terminal-style {
  background: #0f0f1a;
  border: 2px solid #00d9ff;
  border-radius: 16px;
  box-shadow: 0 0 40px rgba(0, 217, 255, 0.3);
}

.terminal-header {
  background: linear-gradient(135deg, #00d9ff, #0099ff);
  padding: 12px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 700;
  font-size: 13px;
  border-radius: 14px 14px 0 0;
}

.terminal-header button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 4px;
  transition: opacity 0.3s;
}

.terminal-header button:hover {
  opacity: 0.7;
}

.terminal-content {
  padding: 20px;
}

.terminal-line {
  display: flex;
  gap: 12px;
  align-items: flex-start;
  margin-bottom: 15px;
}

.prompt {
  color: #00d9ff;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 700;
  font-size: 16px;
  margin-top: 10px;
  flex-shrink: 0;
}

.terminal-input {
  flex: 1;
  background: rgba(0, 217, 255, 0.05);
  border: 1px solid rgba(0, 217, 255, 0.2);
  border-radius: 8px;
  padding: 10px 14px;
  color: white;
  font-family: 'JetBrains Mono', monospace;
  font-size: 13px;
  transition: all 0.3s;
}

.terminal-input:focus {
  outline: none;
  border-color: #00d9ff;
  background: rgba(0, 217, 255, 0.1);
  box-shadow: 0 0 15px rgba(0, 217, 255, 0.2);
}

.terminal-textarea {
  resize: vertical;
  min-height: 80px;
}

.terminal-actions {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.terminal-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 20px;
  background: rgba(0, 217, 255, 0.1);
  border: 1px solid #00d9ff;
  border-radius: 8px;
  color: #00d9ff;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 600;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.3s;
}

.terminal-btn:hover:not(:disabled) {
  background: #00d9ff;
  color: #0f0f1a;
  transform: translateY(-2px);
}

.terminal-btn.whatsapp:hover:not(:disabled) {
  background: #25D366;
  border-color: #25D366;
  color: white;
}

.terminal-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

/* Floating Particles */
.floating-particles {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 1;
}

.particle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: rgba(0, 217, 255, 0.6);
  border-radius: 50%;
  animation: particle-float 20s linear infinite;
  opacity: 0;
}

@keyframes particle-float {
  0% {
    opacity: 0;
    transform: translateY(100vh);
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(-100px);
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Mobile */
@media (max-width: 768px) {
  .bot-companion {
    width: 30px;
    height: 30px;
  }
  
  .language-toggle,
  .budget-button {
    padding: 6px 12px;
    font-size: 12px;
  }
  
  .language-toggle {
    top: 15px;
    right: 15px;
  }
  
  .budget-button {
    top: 15px;
    left: 15px;
  }
  
  .budget-popup {
    max-width: 95%;
  }
  
  .terminal-content {
    padding: 15px;
  }
  
  .terminal-actions {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .bot-companion {
    display: none;
  }
}
