/* FullPage System - ORIGINAL */

.fullpage-container {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
}

.pages-wrapper {
  height: 100vh;
  transition: transform 0.8s cubic-bezier(0.645, 0.045, 0.355, 1);
  will-change: transform;
}

.page {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.page-content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 10;
}

/* Background Elements - ORIGINAL */
.grid-pattern {
  position: fixed;
  inset: -20%;
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.05) 1px, transparent 1px);
  background-size: 60px 60px;
  pointer-events: none;
  z-index: 0;
}

.gradient-blur {
  position: fixed;
  border-radius: 50%;
  filter: blur(100px);
  pointer-events: none;
  opacity: 0.3;
}

.gradient-1 {
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, rgba(0, 217, 255, 0.3) 0%, transparent 70%);
  top: -250px;
  left: -250px;
  z-index: 0;
}

.gradient-2 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.3) 0%, transparent 70%);
  bottom: -200px;
  right: -200px;
  z-index: 0;
}

.gradient-3 {
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(0, 153, 255, 0.2) 0%, transparent 70%);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
}

/* Page Indicators - ORIGINAL */
.page-indicators {
  position: fixed;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.page-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  padding: 0;
}

.page-dot:hover {
  background: rgba(0, 217, 255, 0.5);
  border-color: #00d9ff;
  transform: scale(1.2);
}

.page-dot.active {
  background: #00d9ff;
  border-color: #00d9ff;
  transform: scale(1.3);
}

.dot-label {
  position: absolute;
  right: 25px;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 12px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  color: white;
  font-weight: 600;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.page-dot:hover .dot-label {
  opacity: 1;
}

/* Animations */
.animate-slide-up {
  animation: slideUp 0.8s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile Adjustments */
@media (max-width: 768px) {
  .page-indicators {
    right: 15px;
    gap: 12px;
  }
  
  .page-dot {
    width: 10px;
    height: 10px;
  }
  
  .dot-label {
    display: none;
  }
}
